import React from 'react';

import Navbar from '../../ui/Components/Navbar'
import { Container } from './styled';
import { Input } from '../../ui/Components/Input';
import { useEffect } from 'react';

const Home: React.FC = () => {

  useEffect(()=>{
    fetch('http://localhost/shipados_api/estado/BA', {
      method: "GET",
    })
    .then(dados => dados.json())
    .then(data => console.table(data.resp.cidade))
  },[])

  return (
    <Navbar>
      <Container>
        <h2>Home</h2>

        <form>
          <Input title="Titulo" name="titulo" type="text" />
          <Input title="Data de inicio" name="inicio" type="date" />
          <Input title="Data de fim" name="fim" type="date" />
          <Input title="Local de Venda" name="local" type="text" />
        
        </form>

      </Container>
    </Navbar>
  );
}

export default Home;