import React, { useState } from 'react';

import { Link } from 'react-router-dom';
import { Container, Nav, Body, SideBar, Content, SubNav } from './styled';
import { FiMenu, FiLogOut } from "react-icons/fi";




const Navbar: React.FC = (props) => {
  const [activBar, setActiveBar] = useState(false)
  return (
    <Container>
      <SideBar className={`${activBar ? 'active' : ''}`}>
        
      </SideBar>
      <Content className={`${activBar ? 'active' : ''}`}>
        <Nav>
          <Link to="#" onClick={(ev) => {
            ev.preventDefault()
            setActiveBar(!activBar)
          }}>
            <FiMenu size={40} color={'#fff'} />
          </Link>
          <SubNav>
              <Link to="/login">
                <FiLogOut  size={40} color={'#fff'} />
              </Link>
          </SubNav>
        </Nav>
        <Body>
          {props.children}
        </Body>
      </Content>
    </Container>
  );
}

export default Navbar;