import React from 'react';
import {ThemeProvider} from 'styled-components'
import Routes from './routes';
import { theme } from './ui/Styles/theme';
import {GlobalStyled} from './ui/Styles/GlobalStyled'

function App() {
  return (
    <div className="App">
      <ThemeProvider theme={theme} >
        <Routes />
        <GlobalStyled />
      </ThemeProvider>
    </div>
  );
}

export default App;
