import {InputHTMLAttributes} from "react"
import {ContainerStyled, InputStyled, Label} from './styled'
interface InputProsp extends InputHTMLAttributes<HTMLInputElement> {
  title: string,
  name: string,
  type: string
}
export const Input = ({title, name, type, ...props}: InputProsp) =>{
  return (
    <ContainerStyled>
      <Label htmlFor={name}>{title}</Label>
      <InputStyled type={type} name={name} id={name} {...props}/>
    </ContainerStyled>
  );
}