import { ButtonHTMLAttributes } from 'react'
import { ButtonStyled } from './styled'
interface BtnProps extends ButtonHTMLAttributes<HTMLButtonElement> {
  title: string
  color?: string,
  bg?: string,
  size?: number,
  boxShadow?: Boolean,
  border?: Boolean
}
export const Button = ({ title, color, bg, border, boxShadow, size, ...props }: BtnProps) => {
  return (
    <ButtonStyled
      bg={bg}
      color={color}
      border={border}
      boxShadow={boxShadow}
      size={size}
      {...props}
    >
      {title}
    </ButtonStyled>
  )
}