import React, { useState } from 'react';

import { Input } from '../../ui/Components/Input';
import { Button } from '../../ui/Components/Button';
import BgAdmin from '../../assets/img/bg-admin.jpg'
import Logo from '../../assets/img/logo.png'
import styled from 'styled-components';

interface UserProps {
  email: string,
  senha: string
}

const Login: React.FC = () => {
  const [use, setUse] = useState<UserProps>({
    email: '',
    senha: ''
  })
  return (
    <Container>
      <form onSubmit={(e) => {
        e.preventDefault()
        console.log(use)
      }}>
        <ImgLogo src={Logo} alt="" />
        <Input
          title="E-mail"
          name="email"
          type="email"
          autoComplete="off"
          value={use.email}
          onChange={(ev) => setUse((prev) => ({ ...prev, [ev.target.name]: ev.target.value }))}
        />
        <Input
          type="password"
          name="senha"
          title="Senha"
          value={use.senha}
          onChange={(ev) => setUse((prev) => ({ ...prev, [ev.target.name]: ev.target.value }))}
        />
        <Button title="Entrar" color="#069" bg="#fff" size={20} boxShadow={true} type="submit" />
      </form>
    </Container>
  );
}

export default Login;


const Container = styled.div`
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  background-image: url(${BgAdmin});
  background-size: cover;
  align-items: center;
  form {
    width: 90%;
    max-width: 450px;
    padding: 15px 25px;
    background-color: ${props => props.theme.rgba};
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
`;

const ImgLogo = styled.img`
  width: 200px;
  margin-top: 10px;
  margin-bottom: 15px;
`
