import styled from "styled-components";

interface ButtonProps {
  color?: string,
  bg?: string,
  size?: number,
  boxShadow?: Boolean,
  border?: Boolean
}

export const ButtonStyled = styled.button<ButtonProps>`
    min-width: 250px;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: ${props => props.color ? props.color : props.theme.text};
    font-weight: 500;
    font-size:${props => props.size ? props.size : 16}px;
    border-radius: 25px;
    border:${props => props.border ? '2px solid '+ (props.color ? props.color: props.theme.text ): 'none'};
    background-color: ${props => props.bg ? props.bg : "transparent"};
    box-shadow: ${props => props.boxShadow ? '0 0 7px -3px #000;': 'none'};
    margin: 10px 0;
    cursor: pointer;
`